<template>
  <v-app id="inspire">
    <v-main>
      <v-container fluid fill-height>
        <v-layout align-center justify-center dark>
          <v-flex xs12 sm8 md4>
            <v-card dark class="elevation-12 form-login">
              <v-toolbar dark>
                <v-toolbar-title>AniPlace — Login</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-form @submit.prevent="login()">
                  <v-text-field
                      prepend-icon="mdi-account"
                      v-model="usuario"
                      label="Usuário"
                      type="text"
                      v-on:keyup.enter="login()"
                  ></v-text-field>
                  <v-text-field
                      id="password"
                      prepend-icon="mdi-lock"
                      v-model="senha"
                      label="Senha"
                      type="password"
                      v-on:keyup.enter="login()"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <p class="login-err" v-show="this.$store.state.auth.loginErr">
                  <v-icon>mdi-alert-circle</v-icon>
                  <span>Erro: verifique os dados.</span>
                </p>
                <v-spacer></v-spacer>
                <v-btn :loading="this.$store.state.auth.loading" dark @click="login()">Entrar <v-icon>mdi-login</v-icon></v-btn>
              </v-card-actions>
              <v-card-text>
                <p class="noaccount">Não possui uma conta? <span @click="nav()">Cadastre-se</span> agora mesmo.</p>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import { mapActions } from 'vuex';

export default {
name: "LoginPage",
  data: () => ({
    usuario: "",
    senha: ""
  }),
  methods: {
    ...mapActions('auth', ['ActionLogin']),
    ...mapActions('auth', ['ActionKillSession']),
    login(){
      this.ActionLogin({usuario: this.usuario, senha: this.senha});
    },
    nav(){
      this.ActionKillSession();
      this.$router.push('/cadastro');
    }
  },
  computed: {
  }
}
</script>

<style scoped>
#inspire {
  background-image: url(https://i.imgur.com/zBVQoFx.jpeg);
  background-attachment: fixed;
  background-size: cover;
}

.form-login {
  opacity: 65%;
}

.login-err {
  color: #FF0000;
  margin-bottom: 0;
}

.login-err span {
  margin-left: 4px;
}

.noaccount span {
  cursor: pointer;
  transition: 300ms;
  color: #FFFFFF;
}

.noaccount span:hover {
  color: #FFFFFF69;
}
</style>